<template>
  <v-simple-table dense>
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">Portfolio</th>
          <th class="text-left">Portfolio Name</th>
          <th class="text-left">Value</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in portfolio" :key="item.id">
          <td>{{ item.id }}</td>
          <td>{{ item.name }}</td>
          <td>{{ item.value }}</td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</template>

<script>
import axios from "../../api/index";
export default {
  name: "Portfolio",
  created() {
    axios
      .get("/api/v1/uportfolio")
      .then((res) => {
        this.portfolio = res.data.product;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  data: () => ({
    portfolio: []
  }),

  methods: {},
};
</script>
